body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App-logo{height:100px;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-float infinite 3s ease-in-out;animation:App-logo-float infinite 3s ease-in-out}}.App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin)}.App-link{color:#704cb6}@-webkit-keyframes App-logo-float{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0px)}}@keyframes App-logo-float{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0px)}}body .alert{color:#bc1823;font-size:80%}

.container .input-group.search{background:#FFFFFF;border:1px solid rgba(197,197,197,0.4);box-sizing:border-box;border-radius:20px;width:75%;margin:auto}.container .input-group.search .input-group-text{border-radius:20px}.container .input-group.search .fa{color:#bc1823}.container .input-group.search .form-control{border:none}.container .form-control{height:inherit;font-size:0.8rem}.container .form-control:focus{box-shadow:none}.container input[type="date"]{color:#D5D5D5}.container ::-webkit-input-placeholder{color:#D5D5D5;font-size:14px}.container :-ms-input-placeholder{color:#D5D5D5;font-size:14px}.container ::-ms-input-placeholder{color:#D5D5D5;font-size:14px}.container ::placeholder{color:#D5D5D5;font-size:14px}.container :-moz-placeholder{color:#D5D5D5;font-size:14px}.container :-ms-input-placeholder{color:#D5D5D5;font-size:14px}.container ::-webkit-input-placeholder{color:#D5D5D5;font-size:14px}

.container-fluid .group-name{color:#bc1823;text-transform:uppercase}.container-fluid .btn{border:1px solid #e3757559;padding:.3rem;font-size:0.7rem;line-height:0;border-radius:1rem}.container-fluid .row .main-post{box-shadow:0 0 2px 1px rgba(188,24,35,0.86);border-radius:5px}.container-fluid .row .main-post .img-section{margin:auto}.container-fluid .row .main-post .img-section img{margin:1rem auto}@media (min-width: 768px){.container-fluid .row .main-post .img-section img{width:inherit}}.container-fluid .row .main-post .extra-details{color:#bc1823;padding:15px}.container-fluid .row .main-post .extra-details a{color:#bc1823}.container-fluid .row .main-post .extra-details h6{font-size:80%}.container-fluid .row .main-post .extra-details .actions>*{margin-top:1rem}@media (min-width: 768px){.container-fluid .row .main-post .extra-details .actions{margin-top:9rem}}

